.hero {
  @include desktop {
    max-height: 500px;
  }
}

.hero__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 10px;

  @include desktop {
    gap: 40px;
    flex-direction: row;
    max-width: 556px;
  }
}

.hero__list li {
  display: flex;
  align-items: center;
  gap: 12px;

  @include desktop {
    gap: 0;
  }
}

.hero__list svg {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  fill: $color-primary;

  @include desktop {
    height: 52px;
    width: 54px;
  }
}

.hero__list li p {
  font-size: 15px;
  margin: 0;
  @include desktop {
    font-size: 28px;
  }
}
.hero__inner {
  position: relative;

  @include desktop {
    display: flex;
    justify-content: end;
  }
}

.hero__text {
  display: flex;
  flex-direction: column;
  padding: 32px 40px;

  @include laptop {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    padding: 72px;
  }
}

.hero__text h1 {
  letter-spacing: 0.3px;
  max-width: 500px;
}

.hero__image {
  position: relative;
  min-height: 312px;
  width: 100%;
  object-fit: cover;

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -50vw;
    width: calc(100vw + 49%);
    height: 1px;
    background-color: #dadada;
  }
}

.hero__image img {
  position: relative;
  width: 100%;
  height: 312px;
  object-fit: cover;

  @include laptop {
    height: 575px;
    width: 60%;
    margin-left: auto;
  }

  @include desktop {
    max-width: 1200px;
    width: 100%;
  }
}

.hero__image::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  left: auto;
  width: 100%;
  height: 100%;
  max-width: 1200px;
  background: linear-gradient(
    90deg,
    rgba(238, 238, 238, 0.5) 0%,
    rgba(238, 238, 238, 0.25) 50%,
    transparent 100%
  );
}
