.section-why__cards {
  display: flex;
  flex-wrap: wrap;

  > * {
    @include tablet {
      display: block;
      width: 50%;
    }

    @include laptop {
      counter-reset: chapter 0;
    }
  }

  &:after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;
    left: -40px;
    width: calc(100% + 80px);
    height: 1px;
    background-color: #dadada;

    @include desktop-l {
      left: -100vw;
      width: 200vw;
    }
  }
}

.section-why {
  border-top: 1px solid #dadada;

  @include desktop {
    overflow: hidden;
  }
}

.counter-list {
  counter-reset: my-counter;
}

.counter-list li {
  position: relative;
}

.counter-list li::before {
  counter-increment: my-counter;
  content: counter(my-counter);
  position: absolute;
  background-color: $color-primary;
  color: $color-white;
  font-weight: 500;
  text-align: center;
  z-index: 2;
  border-radius: 50%;
  height: 35px;
  width: 35px;
  padding: 3px;
  font-size: 22px;
  top: 44px;
  left: -17px;

  @include laptop {
    height: 40px;
    width: 40px;
    font-size: 20px;
    top: 16px;
    left: -20px;
    padding: 7px;
  }

  @include desktop {
    height: 70px;
    width: 70px;
    font-size: 40px;

    padding: 10px 14px;
    top: 23%;
    left: -40px;
  }
}

.section-why__header {
  display: flex;
  flex-direction: column-reverse;
  position: relative;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
  }

  @include desktop {
    padding: 0px;
    margin-top: 0;
    height: 350px;
  }

  @include desktop-l {
    height: 400px;
  }

  @media (min-width: 1920px) {
    height: 504px;
  }
}

.section-why__cards-wrap {
  position: relative;
  margin-bottom: 40px;

  @include laptop {
    margin-bottom: 144px;
  }
}

.section-why .section-why__header h2 {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 17px;

  @include desktop {
    font-size: 28px;
  }

  @include desktop-l {
    font-size: 48px;
  }
}

.section-why .section-why__header p {
  font-family: "Geist";
  font-size: 14px;
  font-weight: 300;
  margin: 0;
  line-height: 22px;

  @include desktop {
    font-size: 16px;
    line-height: 28px;
  }

  @include desktop-l {
    font-size: 20px;
    line-height: 38px;
  }
}

.section-why__text {
  padding: 40px;
  min-height: 274px;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: -40px;
    width: calc(100% + 80px);
    height: 1px;
    background-color: #dadada;
  }

  @include tablet {
    width: 50%;
    padding: 30px 15px;
  }

  @include laptop {
    padding: 30px 30px;
  }
  @include desktop {
    width: 54%;
    padding: 36px 50px 30px 70px;
  }

  @include desktop-l {
    padding: 70px 10px 30px 70px;
    width: 54%;
  }

  @media (min-width: 1920px) {
    padding: 130px 10px 30px 70px;
  }
}

.section-why_contact-wrap {
  position: relative;
  display: flex;
  position: relative;
  margin-left: -40px;
  margin-right: -40px;
  width: calc(100% + 80px);

  @include tablet {
    width: 50%;
    margin-right: 0;
    margin-left: 0;
  }

  @include desktop {
    width: 46%;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -80px;
    width: calc(100% + 80px);
    height: 1px;
    background-color: #dadada;

    @include tablet {
      left: -100vw;
      width: 150vw;
    }
  }
}

.section-why_contact-wrap img {
  position: absolute;
  height: 243px;
  width: auto;
  z-index: 2;
  bottom: 0%;
  left: 5%;

  @include tablet {
    height: 274px;
    left: -7%;
  }

  @include laptop {
    height: 275px;
    left: -9%;
  }

  @include desktop {
    height: 370px;
    bottom: 0%;
    left: -6%;
  }

  @include desktop-l {
    padding: 159px 50px 25px 44px;
    height: 530px;
    bottom: -6%;
    left: -20%;
  }
  @media (min-width: 1920px) {
    top: -24%;
    left: -8%;
    height: 650px;
  }
}

.section-why__link-wrap {
  padding: 27px 11px 27px 48%;
  width: 100%;
  background-color: $color-muted;
  font-size: 14px;
  min-height: 233px;

  @media (min-width: 500px) {
    padding-left: 40%;
  }

  @include laptop {
    padding: 30px 30px 30px 25%;
    font-size: 18px;
  }

  @include desktop {
    margin-left: 0px;
    padding: 27px 0px;

    padding: 76px 10px 25px 40%;
  }

  @media (min-width: 1920px) {
    padding: 159px 76px 25px 54px;
    margin-left: 45%;
  }
}

.section-why__header .arrow-bottom {
  position: absolute;
  z-index: 2;
  height: 100px;
  bottom: -9%;
  right: 2%;
  width: 55px;

  @include tablet {
    right: auto;
    height: 80px;
    bottom: -14%;
    left: 35%;
  }

  @include laptop {
    bottom: -14%;
    height: 100px;
  }

  @include desktop {
    height: 120px;
    bottom: -15%;
    left: 45%;
  }

  @include desktop-l {
    z-index: 2;
    bottom: -13%;
    height: 181px;
    left: 43%;
  }
}

.section-why__link-wrap h3 {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 43px;
  line-height: 1.7;

  @include tablet {
    font-size: 18px;
    margin-bottom: 25px;
  }

  @include laptop {
    font-size: 22px;
    margin-bottom: 50px;
    line-height: 1.3;
  }

  @include desktop {
    font-size: 24px;
  }

  @include desktop-l {
    font-size: 36px;
    margin-bottom: 36px;
  }
}

.section-why__link-wrap .button {
  background-color: $color-primary;
  color: $color-white;
  min-height: 54px;
  width: 144px;
  min-height: auto;
  line-height: 1.7;
  width: 144px;
  height: 54px;
  padding: 15px;

  @include laptop {
    width: 244px;
    min-height: 72px;
    padding: 22px 15px;
  }
}

.section-why__cards li:nth-child(odd) {
  @include laptop {
    width: 50%;
  }

  @include desktop-l {
    width: 54% !important;
  }
}

.section-why__cards li:nth-child(even) {
  @include laptop {
    width: 50%;
  }

  @include desktop-l {
    width: 46% !important;
  }
}

.section-why__cards li::after {
  content: " ";
  background: linear-gradient(
    266deg,
    rgba(255, 255, 255, 0.6699054621848739) 0%,
    rgba(255, 255, 255, 1) 79%,
    rgba(255, 255, 255, 0.9220063025210083) 100%
  );
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  opacity: 0.9;
}
